import counter from '../../../components/counter/counter.vue';
export default {
	data() {
		return {
			goodsList: [],
			currentIndex: null, //控制滑动效果，当前滑动下标
			oldIndex: null, //上一个左滑下标
			footerbottom: 0,
			selectedList: [],
			isAllSelected: false,
			sumPrice: '0.00'
		};
	},
	onLoad() {
		// 兼容h5下的底部菜单



	},
	onShow() {
		uni.getStorage({
			key: 'goodsList',
			success: res => {
				//将所有商品到选中状态都设置为false
				for (let i = 0; i < res.data.length; i++) {
					res.data[i].selected = false;
				}
				this.goodsList = res.data;
				// console.log(this.goodsList);
				// 属性数据的初始化
				this.selectedList = [];
				this.isAllSelected = false;
				this.sumPrice = '0.00';
			}
		});
	},
	comments: {
		counter
	},
	methods: {
		handleCheckbox(item) {
			// 单选
			item.selected = !item.selected;
			// 条件: 数组中是否包含该元素
			let isExist = this.selectedList.indexOf(item);
			if (isExist > -1) {
				this.selectedList.splice(isExist, 1);
			} else {
				// push 数组里
				this.selectedList.push(item);
			}
			// 全选状态
			if (this.selectedList.length == this.goodsList.length) {
				this.isAllSelected = true;
			} else {
				this.isAllSelected = false;
			}
			this.sum();
		},
		handleSelectedAll() {
			// 全选
			this.isAllSelected = !this.isAllSelected;
			// 数据处理
			let arr = [];
			this.goodsList.forEach((item, i) => {
				item.selected = this.isAllSelected;
				arr.push(item);
			});
			this.selectedList = this.isAllSelected ? arr : [];
			// 调用合计
			this.sum();
		},
		handleGoodsInfo(item) {
			uni.navigateTo({
				url: '../../goods/goods?goodsInfo=' + JSON.stringify(item)
			});
		},
		handleTouchStar(index, e) {
			// console.log(e);
			//多点触控，不触发
			if (e.touches.length > 1) {
				return;
			}

			this.oldIndex = this.currentIndex;
			this.currentIndex = null;

			//初始化
			this.initXY = [e.touches[0].pageX, e.touches[0].pageY];
		},
		handleTouchMove(index, e) {
			// console.log('move');
			if (e.touches.length > 1) {
				return;
			}
			//移动位置
			let moveX = e.touches[0].pageX - this.initXY[0];
			let moveY = e.touches[0].pageY - this.initXY[1];
			//左滑
			if (moveX < 0) {
				this.currentIndex = index;
			} else {
				//右滑
				// this.oldIndex = this.currentIndex;
				// this.currentIndex = null;
			}
			//滑动距离小，不触发
			if (Math.abs(moveX) < 5) {
				return;
			}
			//竖向滑动，不触发
			if (Math.abs(moveY) > Math.abs(moveX)) {
				return;
			}
		},
		handleTouchEnd(index, e) {
			console.log('end');
		},
		sum() {
			//合计
			this.sumPrice = 0;
			this.goodsList.forEach(item => {
				if (item.selected) {
					this.sumPrice = this.sumPrice + item.number * item.price;
				}
			});
			this.sumPrice = this.sumPrice.toFixed(2);
		},
		handleSingleDelete(item) {
			// 更新storage
			uni.getStorage({
				key: 'goodsList',
				success: res => {
					let goodsList = res.data;
					// 本地删除选中商品
					goodsList.forEach((goods, index) => {
						if (goods.goods_id == item.goods_id) {
							goodsList.splice(index, 1);
						}
					});
					uni.setStorageSync('goodsList', goodsList);
				}
			});

			// 更新数组
			this.goodsList.splice(this.goodsList.indexOf(item), 1);
			this.selectedList.splice(this.selectedList.indexOf(item), 1);

			// 全部删除取消全选状态
			this.isAllSelected = this.goodsList.length > 0 ? this.isAllSelected : false;
			this.sum();
			this.oldIndex = null;
			this.theIndex = null;
		},
		handleMulDelete() {
			// 循环删除所有选中的商品
			while (this.selectedList.length > 0) {
				this.handleSingleDelete(this.selectedList[0]);
			}

			// 初始化数据
			this.selectedList = [];
			this.isAllSelected = false;
			this.sum();
		},
		add(item) {
			item.number++;
			// console.log(this.goodsList)
			// 更新storage
			uni.getStorage({
				key: 'goodsList',
				success: res => {
					uni.setStorageSync('goodsList', this.goodsList);
				}
			});
			this.sum();
		},
		sub(item) {
			if (item.number <= 1) {
				return;
			}
			item.number--;
			// 更新storage
			uni.getStorage({
				key: 'goodsList',
				success: res => {
					uni.setStorageSync('goodsList', this.goodsList);
				}
			});
			this.sum();
		},
		handleConfirm() {
			// 结算
			if (this.selectedList.length < 1) {
				uni.showToast({
					title: '请选择结算的商品',
					icon: 'none'
				});
				return;
			}

			// 本地存储
			uni.setStorage({
				key: 'confirmList',
				data: this.selectedList,
				success: () => {
					uni.navigateTo({
						url: '../../order/order'
					});
				}
			});
		}
	}
};
